<template>
  <div class="rounded-lg bg-white py-4 px-2">
    <h3 class="py-4 px-2">메타버스 관련주를 모아봅니다.</h3>
    <div class="flex justify-center">
      <div class="mb-4 w-64 text-center text-red-600 text-xs font-bold">
        경고: 종목 추천이 아닙니다.<br />
        메타버스 관련주 투자는 2000년 IT버블 때와 비슷한 흐름이 있습니다. <br />
        투자에 주의해주세요
      </div>
    </div>

    <!-- 네트워크 에러 -->
    <div v-if="errorMessage.length > 0" class="p-2 shadow-xl">
      <div class="text-xs text-left">에러메세지:</div>
      <div class="text-lx text-red-600">{{ errorMessage }}</div>
    </div>
    <!-- 필터 -->
    <div v-else class="pb-4">
      <div class="text-xs text-left">선택한 태그:</div>
      <div class="flex flex-wrap items-center">
        <button :class="getTagClass('.all')" @click="toggleTag('.all')">
          전체
        </button>
        <button
          :class="getTagClass(t)"
          @click="toggleTag(t)"
          @mouseover="showTagDesc(t)"
          v-for="t in tags"
          :key="t"
        >
          <small>{{ t }}</small>
        </button>
      </div>
      <div class="p-4 h-24 rounded-lg shadow-xl text-left">
        <div class="pb-2 text-xs text-gray-600">태그 설명:</div>
        <div v-html="lastTagDesc"></div>
      </div>
    </div>
    <!-- 회사 리스트 -->
    <div class="pb-4">
      <div class="grid grid-cols-2 lg:grid-cols-4 md:grid-cols-3 gap-1">
        <div
          class="rounded-lg overflow-hidden shadow-lg"
          v-for="c in companySelected"
          :key="c.code"
        >
          <img
            class="w-full h-32 object-contain"
            :src="getLogoSrc(c)"
            :alt="getLogoSrc(c)"
          />
          <div class="px-2">
            <div class="flex justify-center items-center">
              <div class="">
                {{ c.name }}
              </div>
              <a class="pl-2 w-6" :href="getChartUrl(c)" target="_blank"
                ><SvgChart />
              </a>
            </div>
            <div class="pt-2 text-sm text-left">{{ getDesc(c) }}</div>
            <div class="pt-1 flex flex-wrap items-center">
              <div class="text-sm pr-1">태그:</div>
              <button
                class="tw-tags"
                v-for="t in c.tags"
                :key="t"
                @click="selectTag(t)"
              >
                {{ t }}
              </button>
            </div>
            <div class="pt-1 flex text-xs">
              <div class="pr-2">현재가:</div>
              <div>{{ c.price }}</div>
            </div>
            <div class="flex text-xs">
              <div class="pr-2">시총:</div>
              <div>{{ c.total }}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SvgChart from "@/components/SvgChart.vue";
export default {
  components: { SvgChart },
  data() {
    return {
      loadingData: true,
      errorMessage: "",
      toolUpdateDate: "",
      stockDatas: [],
      //
      tagsDesc: {},
      tags: [],
      selectedTags: {}, // key:tag data:1
      companyList: [],

      lastTagDesc: "",
    };
  },
  async mounted() {
    const reqData = {
      api: "getInvestHome",
      loginToken: localStorage.getItem("loginToken"),
      userUid: this.$store.getters.userUid,
    };

    this.errorMessage = "";
    this.loadingData = true;
    const res = await this.$store.callAsync(reqData);
    this.loadingData = false;

    if (res.code != 100) {
      this.errorMessage = res.message;
      return;
    }
    console.log("getInvestHome", res);

    // this.errorMessage = res.message;
    // if (this.errorEditName.length > 0) return;

    const codeMap = {};
    res.investHome.datas.forEach((data) => {
      codeMap[data.stock_code] = data;
    });

    this.tagsDesc = {
      통신망:
        "통신속도 및 지연을 개선합니다.\n메타버스에서는 속도와 지연은 매우 중요한 요소입니다.",
      플렛폼:
        "사용자에게 수익 실현이 가능한 서비스를 제공합니다.\n수익 실형이 가능하다면 양질의 컨텐츠는 자연히 증가할 것입니다.",
      게임: "게임은 고수익을 추구합니다.",
      음반: "음반과 유튜브로 수익이 발생합니다.",
      하드웨어: "가상현실 관련 장비를 생산합니다.",
      VFX: "가상현실을 실감나게 구현합니다.",
      영상인식: "현실 영상을 빠르게 데이터화 할 수 있는 능력이 잇습니다.",
      "테마주 거부": "메타버스 테마주로 엮이는 것을 거부 했습니다.",
      제작도구:
        "메타버스 구현을 위한 소프트웨어를 제공합니다. 양질의 컨텐츠를 만드는데 도움을 줍니다.",
    };

    this.companyList = [
      {
        name: "SK텔레콤",
        type: "kor",
        code: "017670",
        tags: ["통신망", "플렛폼"],
        desc: "MeetUs - 화상회의 앱",
      },
      {
        name: "NAVER",
        type: "kor",
        code: "035420",
        tags: ["플렛폼", "게임"],
        desc: "ZEPETO (제페토) 앱 서비스",
      },
      {
        name: "JYP",
        type: "kor",
        code: "035900",
        tags: ["음반"],
      },
      {
        name: "에스엠",
        type: "kor",
        code: "041510",
        tags: ["음반"],
      },
      {
        name: "선익시스템",
        type: "kor",
        code: "171090",
        tags: ["하드웨어"],
      },
      {
        name: "위지윅스튜디오",
        type: "kor",
        code: "299900",
        tags: ["VFX"],
      },
      {
        name: "알체라",
        type: "kor",
        code: "347860",
        tags: ["영상인식", "테마주 거부"],
      },
      {
        name: "하이브",
        type: "kor",
        code: "352820",
        tags: ["플렛폼", "음반"],
        desc: "BTS",
      },
      {
        name: "맥스트",
        type: "kor",
        code: "377030",
        tags: ["제작도구"],
        desc: "AR 맵 제작",
      },
      {
        name: "페이스북",
        type: "usa",
        code: "FB.O",
        tags: ["하드웨어비"],
        desc: "오큘러스",
      },
      {
        name: "구글",
        type: "usa",
        code: "GOOGL.O",
        tags: ["클라우드 서비스", "플렛폼"],
      },
      {
        name: "마이크로소프트",
        type: "usa",
        code: "MSFT.O",
        tags: ["클라우드 서비스", "하드웨어"],
        desc: "홀로렌즈",
      },
      {
        name: "로블록스",
        type: "usa",
        code: "RBLX.K",
        tags: ["플렛폼"],
      },
      {
        name: "유니티소프트웨어",
        type: "usa",
        code: "U",
        tags: ["제작도구"],
      },
    ];

    // console.log("codeMap", codeMap);
    this.companyList.forEach((data) => {
      const cdata = codeMap[data.code];
      // console.log("companyList", data.code, cdata);
      if (cdata === undefined) {
        data.price = "---";
        data.total = "---";
        return;
      }
      data.price = cdata.price;
      data.total = cdata.total;
    });

    const tagMap = {};
    this.companyList.forEach((c) => {
      c.tags.forEach((t) => {
        tagMap[t] = 1;
      });
    });

    this.tags = Object.keys(tagMap);
    this.selectedTags = tagMap;
    console.log("this.companyList", this.companyList);
  },
  computed: {
    companySelected() {
      const selected = [];
      this.companyList.forEach((c) => {
        for (let i = 0; i < c.tags.length; i++) {
          const t = c.tags[i];
          if (this.selectedTags[t] === undefined) {
            continue;
          }
          selected.push(c);
          break;
        }
      });
      return selected;
    },
  },
  methods: {
    getLogoSrc(c) {
      return `/stock/logo_${c.code}.png`;
    },
    getChartUrl(c) {
      const code = c.code;
      if (c.type == "kor") {
        return `https://m.stock.naver.com/index.html#/domestic/stock/${code}/total`;
      } else if (c.type == "usa" || c.type == "usa/eft") {
        return `https://m.stock.naver.com/index.html#/worldstock/stock/${code}/total`;
      }
      return "#";
    },
    getDesc(c) {
      if (c.desc === undefined) return "";
      return c.desc;
    },
    getTagClass(t) {
      if (t == ".all") {
        const csel = Object.keys(this.selectedTags).length;
        return csel != this.tags.length ? "tw-tags-off" : "tw-tags";
      }

      return this.selectedTags[t] === undefined ? "tw-tags-off" : "tw-tags";
    },
    toggleTag(t) {
      try {
        if (t == ".all") {
          const csel = Object.keys(this.selectedTags).length;
          if (csel == this.tags.length) {
            this.selectedTags = {};
            return;
          }
          this.tags.forEach((t) => {
            this.selectedTags[t] = 1;
          });
          return;
        }

        if (this.selectedTags[t] === undefined) {
          this.selectedTags[t] = 1;
          return;
        }

        delete this.selectedTags[t];
      } finally {
        this.showTagDesc(t);
      }
    },
    selectTag(t) {
      this.selectedTags = {};
      this.selectedTags[t] = 1;
      this.showTagDesc(t);
    },
    showTagDesc(t) {
      if (this.tagsDesc[t] === undefined) {
        this.lastTagDesc = `${t}`;
        return;
      }

      let desc = this.tagsDesc[t].replace("\n", "<br/>");

      this.lastTagDesc = `
      <p><b>${t}</b> - ${desc}</p>
      `;
    },
  },
};
</script>
